import { $log, ctx } from '../index';
import { ComponentMessageEvent } from '@/types/message/message';

async function request<T = any>(xhr: Partial<XhrOptions>) {
    return new Promise<T>((resolve, reject) => {
        GM_xmlhttpRequest({
            ...xhr,
            onload: res => {
                if (res.status.toString().startsWith("2")) {
                    try {
                        resolve(JSON.parse(res.responseText));
                    } catch (error) {
                        reject(error);
                    }
                } else {
                    reject(res.response);
                }
            },
            onerror: e => {
                reject(e);
            }
        });
    });
}

async function biliApi(params: Partial<XhrOptions>) {
    (params.headers || (params.headers = {}))["Referer"] = "https://www.bilibili.com";

    const data = await request(params);
    console.log(data);
    return data;
}

export default {
    test: /(localhost:8080\/#\/|ddmonitor\.github\.io|ddmonitor\.site)/,
    mode: "top",
    inject() {
        self.addEventListener("message",  e => {
            onMessage(e);
        });
    }
}

async function onMessage(e: ComponentMessageEvent) {
    try {
        if (e.data.type === "M_BILI_REQUEST") {
            postMessage({
                type: "M_BILI_RESPONSE",
                source: ctx.name,
                requestId: e.data.requestId,
                data: await biliApi(e.data.data),
            }, e.origin);  
        } 
    } catch (error) {
        console.error(error)
        postMessage({
            type: "M_ERROR",
            source: ctx.name,
            requestId: e.data.requestId,
            data: error ? (error.message || error) : "Unknow error"
        }, e.origin);
    }
}